.input-style {
  width: 100%;
  height: 100%;
  border-radius: 6px;
  border: solid 1px #d3d3d3;
  padding: 10px;
  font-size: 14px;
  line-height: 16px;
  outline: none;
  font-weight: bold;
  font-family: Roboto;
  &::placeholder {
    font-weight: 500;
  }
}
.disabled {
  cursor: default;
  background-color: #ebebe4;
  opacity: 0.4;
}
